.game {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.help {
  color: #666;
  margin: 10px;
  text-align: center;
}

.left {
  text-align: center;
  width: 250px;
  border: thin solid #ddd;
  box-sizing: border-box;
  height: 217px;
}

.right {
  text-align: center;
  padding: 10px;
  max-width: 250px;
  border: thin solid #ddd;
  box-sizing: border-box;
}

.star {
	display: inline-block;
  margin: 0 15px;
}

.star:after {
  content: "\2605";
  font-size: 45px;
}

.number {
  background-color: #eee;
  border: thin solid #ddd;
  width: 45px;
  height: 45px;
  margin: 10px;
  font-size: 25px;
}

.number:focus, .number:active {
  outline: none;
  border: thin solid #ddd;
}

.timer {
	color: #666;
  margin-top: 3px;
  margin-left: 3px;
  text-align: center;
}

.game-done .message {
  font-size: 250%;
  font-weight: bold;
  margin: 15px;
}